"use client"
import { usePathname } from "next/navigation"
import { SiteBranding } from "@/components/navigation/site-branding"

export function GlobalHeader() {
  const pathname = usePathname()
  if (pathname === "/login") return null
  return (
    <header className="w-full border-b border-gray-200 bg-white py-2 px-4 md:px-6 fixed top-0 left-0 right-0 z-40">
      <div className="max-w-7xl mx-auto">
        <SiteBranding />
      </div>
    </header>
  )
}
